export const mintTokenV3 = async (chainId, accountAddress, bTokenSymbol) => {
  return catchTxApiError(async () => {
    chainId = checkChainId(chainId);
    bTokenSymbol = bTokenSymbol.toUpperCase();
    const configs = [
      {
        chainId: '97',
        address: '0x16728A3d98671c6b304510819D9C59A197CCcdC2',
      },
    ];
    const mintAmounts = {
      BUSD: 10000,
      SXP: 10000,
      ADA: 10000,
      CAKE: 2000,
      MATIC: 5000,
      AAVE: 100,
      TUSD: 10000,
      TRX: 5000,
      BTCB: '0.5',
      ETH: 5,
      LTC: 100,
      XRP: 20000,
    };
    const config = configs.find((c) => c.chainId === chainId);
    const mintAmount = mintAmounts[bTokenSymbol];
    const lensConfig = getDeriLensConfig(DeriEnv.get(), chainId)
    const lens = deriLensFactory(chainId, lensConfig.address)
    const tokens = await lens.getMarketsInfo('0x5b1a7AEB15EB5380EB35ceC8B40438EcB6D51018')
    const bTokenInfo = tokens.find((b) => b.underlyingSymbol === bTokenSymbol)
    if (config && mintAmount && bTokenInfo) {
      const tokenVault = tokenVaultFactory(chainId, config.address);
      return await tokenVault.withdraw(
        accountAddress,
        bTokenInfo.underlying,
        toWei(mintAmount)
      );
    } else {
      throw new Error(
        `unsupported chainId(${chainId}) or bTokenSymbol(${bTokenSymbol}) for mintTokenV3`
      );
    }
  });
};