import { getPoolVersionId } from '../shared/config/pool';
import { unlockDeri } from '../shared/api';
import { api } from './api_shared'

export const unlock = async (
  chainId,
  poolAddress,
  accountAddress,
  bTokenId
) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    // for deri
    return unlockDeri(chainId, poolAddress);
  }
  return api(versionId).unlock(chainId, poolAddress, accountAddress, bTokenId)
};

export const depositMargin = async (
  chainId,
  poolAddress,
  accountAddress,
  amount,
  bTokenId
) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    return { success: false, response: versionId.response };
  }
  return api(versionId).depositMargin(
    chainId,
    poolAddress,
    accountAddress,
    amount,
    bTokenId,
  );
};

