import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import Announcement from './components/Announcement/Announcement'
import Intro from './components/Intro/Intro'
import PageRouter from './pages/PageRouter';
import { useLocation } from 'react-router-dom'
import { DeriEnv } from './lib/web3js/index'
import './app.less';
import './assets/css/fonts.less'
import { inject, observer } from 'mobx-react';
function App() {
  const env = DeriEnv.get()
  const location = useLocation();
  const curRouterClass = location.pathname.split('/')[1]
  const trade = location.pathname.split('/')[2]
  return (
    <div className={`deri-root ${curRouterClass}  ${trade}`}>
      {env === 'testnet' && <Announcement />}
      {trade === 'powers' && <Intro />}
      <Header></Header>
      <PageRouter></PageRouter>
    </div>
  );
}

export default App
